


























import {Vue, Component, Prop} from 'vue-property-decorator';
import {appApi} from '@/api';

@Component({
  name: 'TabPaneContent',
})
export default class extends Vue {
  @Prop() private type!: string;
  public list: any[] = [];
  private loading: boolean = false;
  private multipleSelection: any[] = [];

  private changePageHandler(n: number) {
    this.getMessage();
  }

  private handleSelectionChange(val: any) {
    this.multipleSelection = val;
  }

  private deleteHandler() {
    if (this.multipleSelection.length === 0) {
      this.$message.warning('');
      return;
    }
    this.$confirm('', '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning',
    }).then(() => {
      this.$message({type: 'success', message: '!'});
      // 
      this.getMessage();
    }).catch(() => {
      this.$message({type: 'info', message: ''});
    });
  }

  private markHandler(type: string | undefined) {
    if (Object.prototype.toString.call(type) === '[object MouseEvent]') {
      if (this.multipleSelection.length === 0) {
        this.$message.warning('');
        return;
      }
      this.$message.success('');
      // 
      this.getMessage();
    } else if (type === 'all') {
      this.$message.success('');
    }
  }

  public getMessage() {
    this.loading = true;
    appApi.message({type: this.type}).then((data: any) => {
      this.list = data.data.list;
      this.loading = false;
    });
  }

}
