// axios
import axios from 'axios';
import {Message} from 'element-ui';
import {UserModule} from '@/store/modules/user';

// axios
const httpService = axios.create({
  // url-'https://some-domain.com/api/'
  baseURL: '', // 
  // 
  timeout: 3000, // 
  headers: {'Content-Type': 'application/json'},
});

// request
httpService.interceptors.request.use(
  (config: any) => {
    // token
    if (UserModule.token) {
      config.headers.Authorization = UserModule.token;
    }
    return config;
  },
  (error) => {
    // 
    Promise.reject(error);
  },
);

// respone
httpService.interceptors.response.use(
  (response) => {
    // 
    const res = response.data;
    if (+res.code !== 200) { // 
      if (+res.code === 511) {
        Message({
          message: res.message,
          type: 'error',
          duration: 5 * 1000,
        });
      }
      // 
      return Promise.reject({
        status: res.code,
        message: res.message,
      });
    } else {
      return response.data;
    }
  },
  // 
  (error) => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          error.message = '';
          break;
        case 401:
          error.message = '';
          break;
        case 403:
          error.message = '';
          break;
        case 404:
          error.message = ',';
          break;
        case 405:
          error.message = '';
          break;
        case 408:
          error.message = '';
          break;
        case 500:
          error.message = '';
          break;
        case 501:
          error.message = '';
          break;
        case 502:
          error.message = '';
          break;
        case 503:
          error.message = '';
          break;
        case 504:
          error.message = '';
          break;
        case 505:
          error.message = 'http';
          break;
        default:
          error.message = `${error.response.status}`;
      }
    } else {
      error.message = '';
    }
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000,
    });
    return Promise.reject(error);
  },
);

/**/

/*
 *  get
 *  url:
 *  params:
 * */
export function get(url: string | undefined, params = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url,
      method: 'get',
      params,
    }).then((response) => {
      resolve(response);
    }).catch((error) => {
      reject(error);
    });
  });
}

/*
 *  post
 *  url:
 *  params:
 * */
export function post(url: string | undefined, params = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url,
      method: 'post',
      data: params,
    }).then((response) => {
      resolve(response);
    }).catch((error) => {
      reject(error);
    });
  });
}

/*
 *  
 *  url:
 *  params:
 * */
export function fileUpload(url: string | undefined, params = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url,
      method: 'post',
      data: params,
      headers: {'Content-Type': 'multipart/form-data'},
    }).then((response) => {
      resolve(response);
    }).catch((error) => {
      reject(error);
    });
  });
}

export default {
  get,
  post,
  fileUpload,
};
